body {
  margin: 0;
  padding: 0;
  font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif; 
  font-weight: 600;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  color : white;  
  cursor: default;
}


h2{
    text-align: center;   
    padding-top: 0.8%; 
    padding-bottom: 2%;
    font-weight: 300;
    font-size: 20px;
    letter-spacing: 0.08em;
    color : white;
}

.author{
    font-size : 9px;
    font-weight: 200;
    letter-spacing: 0.1em;
    position: absolute;
    bottom : 0.8%;
    right : 1.6%;
}

.list{
    margin : 0 2% 0 2%;
    cursor: default;
    width : 96%;
    height : 70%;
    /* background-color : gray; */
    display : flex;
    justify-content: center; 
}

.content{
    padding : 2px;
}

.content :hover{
    color : #5200ff;
}

.content-wrapper {
    width : 22%;
    margin : 0 16px 0 16px;
}

.index{
    color : gray;
}

.page-wrapper {
    margin : 0 2% 0 2%;
    width : 96%;
    height : 20%;
    /* background-color : yellow; */
}

.page  {
    text-align: center;
    padding-top: 2%;
}
